const data = `
#数列
$$ a_1,a_2,a_3\\cdots,a_{n},\\cdots $$
$$ \\{a_n\\} $$
#等差数列の和
$$ a_n=a_1+(n-1)d $$
$$ S_n=\\sum_{m=1}^n a_m=\\frac{n\\{2a_1+(n-1)d\\}}{2}=\\frac{n(a_1+a_n)}{2} $$
#等比数列の和
$$ a_n=a_1r^{n-1} $$
$$ S_n=\\sum_{m=1}^n a_m=\\frac{a_1(1-r^n)}{1-r}=\\frac{a_1(r^n-1)}{r-1} $$
$$ S_n=\\sum_{m=1}^n a_m=a_1n $$
#Σkの計算式
$$ 1+2+3+\\cdots+n $$
$$ = \\sum_{k=1}^n k = \\frac{n(n+1)}{2} $$
#Σkの２乗の計算式
$$ 1^2+2^2+3^2+\\cdots+n^2 $$
$$ = \\sum_{k=1}^n k^2 = \\frac{n(n+1)(2n+1)}{6} $$
#Σkの３乗の計算式
$$ 1^3+2^3+3^3+\\cdots+n^3 $$
$$ = \\sum_{k=1}^n k^3 = \\left\\{\\frac{n(n+1)}{2}\\right\\}^2 $$
#Σkの４乗の計算式
$$ 1^4+2^4+3^4+\\cdots+n^4 $$
$$ = \\sum_{k=1}^n k^4 = \\frac{1}{30}n(n+1)(2n+1)(3n^2+3n-1) $$
#二項定理
$$ (a+b)^n={}_n C_0a^n+{}_n C_1a^{n-1}b+{}_n C_2a^{n-2}b^2+\\cdots+{}_n C_ra^{n-r}b^r+\\cdots+{}_n C_nb^n $$
$$ =\\sum_{r=0}^n{}_n C_r a^{n-r}b^r $$
#無限級数
$$ \\sum_{n=1}^\\infty a_n = a_1+a_2+\\cdots+a_n+\\cdots $$
#無限級数の収束・発散
$$ \\lim_{n\\to\\infty}a_n=0 $$
$$ \\lim_{n\\to\\infty}a_n\\neq0 $$
#漸化式
`

