import React, { Component } from 'react';
import Page from '../../components/Page';
import MathJax from '../../components/MathJax';

const data = `
#塁乗
$$ \\underbrace{a\\times a\\times \\cdots \\times a\\times a}_n = a^n $$
$$ a^0=1 $$
$$ a^{-n}=\\frac{1}{a^n} $$
$$ a^{\\frac{m}{n}}=\\sqrt[n]{a^m} $$
#底
$$ a $$
#指数
$$ n $$
#指数法則
$$ a^m \\cdot a^n = a^{m+n} $$
$$ \\left(a^m\\right)^n = a^{mn} $$
$$ \\left(ab\\right)^m = a^m b^m $$
$$ \\frac{a^m}{a^n}=a^{m-n} $$
$$ \\left(\\frac{a}{b}\\right)^m=\\frac{a^m}{b^m} $$
#累乗根（nが奇数のとき）
$$ x^n = a $$
$$ x = \\sqrt[n]{a} $$
#累乗根（nが偶数のとき）
$$ x^n = a $$
$$ x = \\pm\\sqrt[n]{a} $$
#累乗根の公式
$$ \\sqrt[n]{a}\\sqrt[n]{b} = \\sqrt[n]{ab} $$
$$ \\frac{\\sqrt[n]{a}}{\\sqrt[n]{b}} = \\sqrt[n]{\\frac{a}{b}} $$
$$ \\left(\\sqrt[n]{a}\\right)^m = \\sqrt[n]{a^m} $$
$$ \\sqrt[m]{\\sqrt[n]{a}} = \\sqrt[mn]{a} $$
$$ \\sqrt[n]{a^m} = \\sqrt[np]{a^{mp}} $$
#指数関数の微分
$$ \\left(e^n\\right)' = e^n $$
$$ \\left(a^n\\right)' = a^n\\log{a} $$
#指数関数の積分
$$ \\int e^xdx = e^x + C $$
$$ \\int a^xdx = \\frac{a^x}{\\log{a}} + C $$

#対数
$$ a^r = R $$
$$ r = \\log_a R $$
#対数計算の基本
$$ \\log_a RS = \\log_aR+\\log_aS $$
$$ \\log_a\\frac{R}{S}=\\log_aR-\\log_aS $$
$$ \\log_aR^t=t\\log_aR $$
$$ \\log_aa=1 $$
$$ \\log_a1=0 $$
#底の変換公式
$$ \\log_aR=\\frac{\\log_bR}{\\log_ba} $$
#対数関数
$$ y = \\log_a x $$
#自然対数
$$ \\log_e x $$
$$ \\log x $$
#常用対数
$$ \\log_{10} x $$
#対数関数の微分
$$ \\left(\\log x\\right)'=\\frac{1}{x} $$
$$ \\left(\\log_a x\\right)'=\\frac{1}{x\\log{a}} $$
#対数関数の積分
$$ \\int\\log{x}dx=x\\left(\\log{x}-1\\right)+C $$
`

export default function({ pageInfo }) {
  return (
    <Page
      title={pageInfo.title}
      page={pageInfo.page}
      maxPage={pageInfo.maxPage}
      category={pageInfo.category}
      data={data}
    />
  )
}
